import { CompserviceService } from './../../compservice.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  competitions = [];
  users = [];

  constructor(private compservice: CompserviceService) { }

  ngOnInit(): void {
    this.compservice.getCompetitions().subscribe((res) => {this.competitions = res; }, (err) => {console.log(err); } );
    this.compservice.getUsers().subscribe((res) => {this.users = res; }, (err) => {console.log(err); } );
  }

}
