import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Competition } from './competition.model';
import { Subject, Observable } from 'rxjs';
import { tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class CompserviceService {

  // competitions: Competition[];


  // tslint:disable-next-line: variable-name

  readonly compurl = 'api/competitions';
  readonly platinumcompurl = 'api/platinumcompetitions';
  readonly goldcompurl = 'api/goldcompetitions';
  readonly silvercompurl = 'api/silvercompetitions';
  readonly freecompurl = 'api/freecompetitions';

  readonly userurl = 'api/users';
  readonly usercount = 'api/userscount';
  readonly oneuser = 'api/oneuser/';
  readonly proupdate = 'api/userupdate/';

  readonly addcomp = 'api/insertcompetition';
  readonly onecomp = 'api/onecompetition/:id';

  compId: string;
  // tslint:disable-next-line: variable-name
  constructor(private http: HttpClient) { }

  private refreshneed = new Subject<void>();

  get refresh() {
    return this.refreshneed;
    console.log('refreshed...');
  }

  getCompetitions() {
    return this.http.get<any>(this.compurl);
  }
  getOneCompetition() {
    return this.http.get<any>(this.onecomp);
  }
  getplatinumCompetitions() {
    return this.http.get<any>(this.platinumcompurl);
  }
  getgoldCompetitions() {
    return this.http.get<any>(this.goldcompurl);
  }
  getsilverCompetitions() {
    return this.http.get<any>(this.silvercompurl);
  }
  getfreeCompetitions() {
    return this.http.get<any>(this.freecompurl);
  }

  getUsers() {
    return this.http.get<any>(this.userurl);
  }

  getOneUser(id) {
    return this.http.get<any>(this.oneuser + id);
  }

  profileupdate(id, fd) {
    return this.http
            .patch<any>(this.proupdate + id, fd)
            .pipe (
              tap (() => {
                this.refreshneed.next();
                console.log('refreshed...by tap cs');
              })
            );
  }
  insertcomp(competition: any) {
    return this.http.post<any>(this.addcomp, competition);
  }



}


