// import { TransactionsComponent } from './../adminlayout/admindash/transactions/transactions.component';
import { MyactivityComponent } from './userdeshboard/myactivity/myactivity.component';
import { TransactionsComponent } from './userdeshboard/transactions/transactions.component';
import { ProfileComponent } from './userdeshboard/profile/profile.component';
import { UserdeshboardComponent } from './userdeshboard/userdeshboard.component';
import { HomeComponent } from './home/home.component';
import { MainlayoutComponent } from './mainlayout.component';
import { FreeComponent } from './../competitions/free/free.component';
import { PlatinumComponent } from './../competitions/platinum/platinum.component';
import { SilverComponent } from './../competitions/silver/silver.component';
import { ListComponent } from './../users/list/list.component';
import { GoldComponent } from './../competitions/gold/gold.component';
import { CompetitionsComponent } from './../competitions/competitions.component';
import { TicketSelectionComponent } from './ticket-selection/ticket-selection.component';
// import { UserlistComponent } from './admindash/userlist/userlist.component';
// import { AdmindashComponent } from './admindash/admindash.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ContactComponent } from './contact/contact.component';
import { RegisterComponent } from './register/register.component';
import { LoginComponent } from './login/login.component';
import { AboutComponent } from './about/about.component';
import { TeamComponent } from './team/team.component';
import { ClientComponent } from './client/client.component';
import { SliderComponent } from './slider/slider.component';
import { AccountTermsComponent } from './account-terms/account-terms.component';
import { DisclaimerComponent } from './disclaimer/disclaimer.component';
import { AuthGuard } from '../auth.guard';
// import { WinnerslistComponent } from './admindash/winnerslist/winnerslist.component';



const mainroutes: Routes = [
  {
     path: 'mainlayout',
     component: MainlayoutComponent,
     children: [

    {
         path: 'competitions', component: CompetitionsComponent,
        children: [
            { path: 'gold', component: GoldComponent},
            { path: 'silver', component: SilverComponent},
            { path: 'platinum', component: PlatinumComponent},
            { path: 'free', component: FreeComponent},

        ]
    },
    {   path: 'contact', component: ContactComponent },
    {   path: 'about', component: AboutComponent},
    {   path: 'team', component: TeamComponent},
    {   path: 'register', component: RegisterComponent},
    {   path: 'login', component: LoginComponent},
    {   path: 'client', component: ClientComponent },
    {   path: 'slider', component: SliderComponent},
    {   path: 'account-terms', component: AccountTermsComponent},
    {   path: 'disclaimer', component: DisclaimerComponent},
    {   path: 'list', component: ListComponent },
    {  path: 'ticket-selection/:id', component: TicketSelectionComponent, canActivate: [AuthGuard] },
    {  path: 'userdashboard', component: UserdeshboardComponent,
                children: [
                  { path: 'profile', component: ProfileComponent},
                  { path: 'transactions', component: TransactionsComponent},
                  { path: 'myactivity', component: MyactivityComponent}
              ]},
    {   path: 'home', component: HomeComponent },
    // { path: 'competitions/:id', component: TicketSelectionComponent },

    ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(mainroutes)],
  exports: [RouterModule]
})
export class MainRoutingModule { }
