import { CompserviceService } from './../../../compservice.service';
import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import * as jwt_decode from 'jwt-decode';


@Component({
  selector: 'app-transactions',
  templateUrl: './transactions.component.html',
  styleUrls: ['./transactions.component.css']
})

export class TransactionsComponent implements OnInit {


  userId: '';
  loggeduser: any = {};
  transactions: any = [];

  competitions = [];
  users = [];
  constructor(private http: HttpClient, private compservice: CompserviceService) { }

  ngOnInit(): void {
    console.log('this is transactions........');

    const token = localStorage.getItem('token');
    // console.log(token);
    this.getDecodedToken(token);

    this.userId = this.loggeduser._id;

    console.log(this.userId);

    this.http.get(`api/transactions/${this.userId}`)
                .subscribe(
                  (res) => {this.transactions = res; }, (err) => {console.log(err); } );

    this.compservice.getCompetitions().subscribe((res) => {this.competitions = res; }, (err) => {console.log(err); } );
    this.compservice.getUsers().subscribe((res) => {this.users = res; }, (err) => {console.log(err); } );

  }

  getDecodedToken(token: string): any {
    try {
    this.loggeduser = jwt_decode(token).subject;
    } catch (Error) {
      return null;
    }
  }


}
