import { HttpClient } from '@angular/common/http';
import { CompserviceService } from './../../../compservice.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-addcomp',
  templateUrl: './addcomp.component.html',
  styleUrls: ['./addcomp.component.css']
})
export class AddcompComponent implements OnInit {
  selectedfile: File = null;
  insertcompdata = {
    title: '',
    type: '',
    detail: '',
    ticket_price: '',
    insertdate: '',
    enddate: '',
    image: ''
  };

  constructor( private compservice: CompserviceService, private http: HttpClient) { }

  ngOnInit(): void {
  }

  selectimage(event) {
    this.selectedfile = event.target.files[0];
    // this.insertcompdata.get('image').setValue(selectedfile);
  }

  insertcomp() {
    // this.insertcompdata.append('image', this.selectedfile, this.selectedfile.name);
    const fd = new FormData();
    fd.append('image', this.selectedfile, this.selectedfile.name);
    fd.append('title', this.insertcompdata.title);
    fd.append('type', this.insertcompdata.type);
    fd.append('detail', this.insertcompdata.detail);
    fd.append('ticket_price', this.insertcompdata.ticket_price);
    fd.append('insertdate', this.insertcompdata.insertdate);
    fd.append('enddate', this.insertcompdata.enddate);

    // this.compservice.insertcomp(fd).subscribe( (res) => {console.log(res); }, (err) => {console.log(err); });
    // const fd = new FormData();
    // fd.append('image', this.selectedfile, this.selectedfile.name);
    this.http.post('api/insertcompetition', fd).subscribe(res => console.log(res));
  }

}
