import { ActivatedRoute, Router } from '@angular/router';
import { CompserviceService } from './../../compservice.service';
import { Component, OnInit, Inject, forwardRef, ɵConsole } from '@angular/core';
import { Route } from '@angular/compiler/src/core';

@Component({
  selector: 'app-platinum',
  templateUrl: './platinum.component.html',
  styleUrls: ['./platinum.component.css']
})
export class PlatinumComponent implements OnInit {

  competitions = [];
  constructor(
    @Inject(forwardRef(() => CompserviceService)) private compservice: CompserviceService,
    private router: Router, private route: ActivatedRoute) {
  }

  ngOnInit(): void {
    this.compservice.getplatinumCompetitions().subscribe((res) => {this.competitions = res; }, (err) => {console.log(err); } );

  }

  onselect(competition) {
    // console.log(competition._id)
    this.router.navigate(['mainlayout/ticket-selection', competition._id]);
  }
}
