import { HttpClient } from '@angular/common/http';
import { CompserviceService } from './../../compservice.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { Route } from '@angular/compiler/src/core';
import * as jwt_decode from 'jwt-decode';


@Component({
  selector: 'app-userdeshboard',
  templateUrl: './userdeshboard.component.html',
  styleUrls: ['./userdeshboard.component.css']
})
export class UserdeshboardComponent implements OnInit {

  constructor( private router: Router,
               private route: ActivatedRoute,
               public compservice: CompserviceService,
               private http: HttpClient) { }
  edit = false;
  selected = '';

  loggeduser = {} as any;
  userId: any;

  public users: any = {
    fullname: '',
    email: '',
    password: '',
    gender: '',
    birthdate: '',
    city: '',
    state: '',
    pin: '',
    address: '',
    phone: '',
    image: '',
    comps: []
  };

  ngOnInit() {
    this.select('myactivity');

    const token = localStorage.getItem('token');
    console.log(token);
    this.getDecodedToken(token);
    this.userId = this.loggeduser._id;
    console.log( this.userId );

    // this.users = this.loggeduser;

    this.http.get(`api/oneuser/${this.userId}`)
    .subscribe(
      (res) => {this.users = res; }, (err) => {console.log(err); } );
    const id = this.users.email;
    console.log(id);
  }

  editmode(edit) {
    this.edit = !edit;
  }
  select(item) {
    this.selected = item;
    this.router.navigate([item], { relativeTo: this.route});
    // $('#nav').addClass('active');
  }
  getDecodedToken(token: string): any {
    try {
    this.loggeduser = jwt_decode(token).subject;
    } catch (Error) {
      return null;
    }
  }

  selectactivity() {
    this.selected = 'myactivity';
    this.router.navigate(['mainlayout/userdashboard/myactivity']);
    // $('#nav').addClass('active');
  }

  selectprofile() {
    this.selected = 'profile';
    this.router.navigate(['mainlayout/userdashboard/profile']);
    // $('#nav').addClass('active');
  }
  selecttransaction() {
    this.selected = 'transactions';
    this.router.navigate(['mainlayout/userdashboard/transactions']);

    // $('#nav').addClass('active');
  }
}
