import { Router, ActivatedRoute } from '@angular/router';
import { CompserviceService } from './../../compservice.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  loaded = false;

  platinumcompetitions = [];
  goldcompetitions = [];
  silvercompetitions = [];
  freecompetitions = [];

  users = [];

  constructor(private compservice: CompserviceService, private router: Router,  private route: ActivatedRoute) { }

  ngOnInit(): void {
    this.compservice.getplatinumCompetitions().subscribe((res) => {this.platinumcompetitions = res; }, (err) => {console.log(err); } );
    this.compservice.getgoldCompetitions().subscribe((res) => {this.goldcompetitions = res; }, (err) => {console.log(err); } );
    this.compservice.getsilverCompetitions().subscribe((res) => {this.silvercompetitions = res; }, (err) => {console.log(err); } );
    this.compservice.getfreeCompetitions().subscribe((res) => {this.freecompetitions = res; }, (err) => {console.log(err); } );

    this.compservice.refresh.subscribe(() => {
    });

    this.compservice.getUsers().subscribe((res) => {this.users = res; }, (err) => {console.log(err); } );

    this.loaded = true;
  }
  onselect(competition) {
    // console.log(competition._id)
    this.router.navigate(['mainlayout/ticket-selection', competition._id]);
  }

onbrowseplat() {
  this.router.navigate(['mainlayout/competitions/platinum']);
}
onbrowsegold() {
  this.router.navigate(['mainlayout/competitions/gold']);
}
onbrowsepsilver() {
  this.router.navigate(['mainlayout/competitions/silver']);
}
onbrowsefree() {
  this.router.navigate(['mainlayout/competitions/free']);
}


onparticipatenow() {
  this.router.navigate(['mainlayout/competitions']);

}
}
