import { CompserviceService } from './../../../compservice.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Component, OnInit, Inject, forwardRef } from '@angular/core';

@Component({
  selector: 'app-competitionslist',
  templateUrl: './competitionslist.component.html',
  styleUrls: ['./competitionslist.component.css']
})
export class CompetitionslistComponent implements OnInit {

  competitions = [];
  constructor(
    @Inject(forwardRef(() => CompserviceService)) private compservice: CompserviceService,
    private router: Router, private route: ActivatedRoute) {
  }

  ngOnInit() {
    // tslint:disable-next-line: max-line-length
    this.compservice.getCompetitions().subscribe((res) => {this.competitions = res; }, (err) => {console.log(err); } );
  }

  select(item) {
// this.selected = item;
    this.router.navigate([item], {relativeTo: this.route});
// $('#nav').addClass('active');
  }

    addcomp() {
    this.router.navigate(['admindash/addcomp'], {relativeTo: this.route});
    }

}
