import { HttpClient } from '@angular/common/http';
import { CompserviceService } from './../../../compservice.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import * as jwt_decode from 'jwt-decode';


@Component({
  selector: 'app-myactivity',
  templateUrl: './myactivity.component.html',
  styleUrls: ['./myactivity.component.css']
})
export class MyactivityComponent implements OnInit {

  loggeduser = {} as any;
  userId: any;

  public users: any = {
    fullname: '',
    email: '',
    password: '',
    gender: '',
    birthdate: '',
    city: '',
    state: '',
    pin: '',
    address: '',
    phone: '',
    image: '',
    comps: []
  };

  competitions = [];

  constructor(private router: Router, private route: ActivatedRoute, public compservice: CompserviceService,
              private http: HttpClient) { }

  ngOnInit(): void {

    const token = localStorage.getItem('token');
    console.log(token);
    this.getDecodedToken(token);
    this.userId = this.loggeduser._id;
    console.log( this.userId );

    // this.users = this.loggeduser;

    this.http.get(`api/oneuser/${this.userId}`)
    .subscribe(
      (res) => {this.users = res; }, (err) => {console.log(err); } );
    const id = this.users.email;
    console.log(id);

    this.compservice.getCompetitions().subscribe((res) => {this.competitions = res; }, (err) => {console.log(err); } );

    // console.log(this.users.gender);
    // console.log(this.userselect);
  }


  getDecodedToken(token: string): any {
    try {
    this.loggeduser = jwt_decode(token).subject;
    } catch (Error) {
      return null;
    }
  }
}
