import { CompserviceService } from './../../compservice.service';
import { tap } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  // tslint:disable-next-line: variable-name
  // private _registerurl = 'http://localhost:3000/api/register';
  // tslint:disable-next-line: variable-name
  private _registerurl = 'api/register';

  // tslint:disable-next-line: variable-name
  // private _loginurl = 'http://localhost:3000/api/login';
  // tslint:disable-next-line: variable-name
  private _loginurl = 'api/login';

  constructor(private http: HttpClient, private compservice: CompserviceService) { }

  private refreshneed = new Subject<void>();

  get refresh() {
    return this.refreshneed;
  }

  registeruser(user: any) {
    return this.http.post<any>(this._registerurl, user);
  }

  loginuser(user: any) {
    return this.http.post<any>(this._loginurl, user)
    .pipe (
      tap (() => {
        this.refreshneed.next();
        console.log('refreshed...by tap cs');
      }));
  }

  logoutuser() {
    return localStorage.removeItem('token');
  }

  loggedIn() {
    return !!localStorage.getItem('token');
  }

}
